
import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { LoginguardGuard } from './login/loginGuard';
import { LoginComponent } from './login/login.component';
import { AuthguardGuard } from './login/authguard.guard';
import { AdminComponent } from './admin.component';
import { AddPortfolioComponent } from './AddPortfolio/AddPortfolio.component';
const backRoutes: Routes = [

    {
        path: "",
        component: AdminComponent,
        children: [
            {
                path: "loginP",
                component: LoginComponent
            },
            {
                path: "AddPortfolio",
                component: AddPortfolioComponent
            }
        ]
    },

];

@NgModule({
    imports: [RouterModule.forChild(backRoutes)],
    exports: [RouterModule]
})
export class BackRoutingModule { }
